








































































import HeadTtop from '../../../../components/head/head.vue'
export default {
	components: {
		'HeadTtop': HeadTtop
	},
	data() {
		return {
			Title: '我的订单',
			backshow: true,
			topsrc: '../../../../static/images/back.png',
			orderTypeindex: 0,
			status: 0,
			page: 1,
			onReachBottomshow: true,
			list: [],
			userinfo: []
		}
	},
	onLoad() {
		this.userinfo = uni.getStorageSync('userinfo')
	},
	onShow() {
		this.page = 1
		this.onReachBottomshow = true
		this.getData()
	},
	onReachBottom() {
		if (this.onReachBottomshow) {
			this.page++
			this.getData()
		}
	},
	methods: {
		orderTypeClic(index) {
			this.orderTypeindex = index
			if (this.orderTypeindex == 0) { // 全部
				this.status = 0
			} else if (this.orderTypeindex == 1) { //代付款
				this.status = 1
			} else if (this.orderTypeindex == 2) { //待取货
				this.status = 2
			} else if (this.orderTypeindex == 3) { // 租赁中
				this.status = 3
			} else { //已完成
				this.status = 5
			}
			this.page = 1;
			this.onReachBottomshow = true
			this.getData()
		},
		orderliClick(id) {
			uni.navigateTo({
				url: "/pages/info/order/details/details?order_id=" + id
			})
		},
		cancelOrder(order_id) { //取消订单
			var that = this;
			let params = {
				order_id: order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.request(false, 'user/cancelOrder', params, 'POST', res => {
				if (res.code == 200) {
					uni.showToast({
						title: '成功',
						duration: 600,
						success() {
							setTimeout(res => {
								that.page = 1
								that.onReachBottomshow = true
								that.getData()
							}, 600)
						}
					});
				}
			});
		},
		confirmOrder(order_id) { //确定收货
			var that = this;
			let params = {
				order_id: order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.request(false, 'user/confirmOrder', params, 'POST', res => {
				if (res.code == 200) {
					uni.showToast({
						title: '成功',
						duration: 600,
						success() {
							setTimeout(res => {
								that.page = 1
								that.onReachBottomshow = true
								that.getData()
							}, 600)
						}
					});
				}
			});
		},
		finishOrder(order_id) { //完成订单
			var that = this;
			let params = {
				order_id: order_id,
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.request(false, 'user/finishOrder', params, 'POST', res => {
				if (res.code == 200) {
					uni.showToast({
						title: '成功',
						duration: 600,
						success() {
							setTimeout(res => {
								that.page = 1
								that.onReachBottomshow = true
								that.getData()
							}, 600)
						}
					});
				}
			});
		},
		apppayOrder(order_id,allprice) { //订单支付
			var that = this;
			let params = {
				order_id: order_id,
				_token: uni.getStorageSync('userinfo')._token
			}

			if (that.userinfo.is_vip == 2) {
				uni.showActionSheet({
					itemList: ['微信支付', '支付宝支付'],
					success: function(res) {
						if (res.tapIndex === 0) {
							that.$http.HttpRequst.request(true, 'order/wxPay', params, 'POST', res => { //微信支付
								console.log(JSON.stringify(res.data))
								uni.requestPayment({
									provider: 'wxpay',
									orderInfo: res.data, //订单数据
									success: function(res) {
										uni.showToast({
											title: '支付成功',
											icon: 'success',
											duration: 800,
											success() {
												setTimeout(res => {
													that.page = 1
													that.onReachBottomshow = true
													that.getData()
												}, 800)
											}
										});
									},
									fail: function(err) {
										uni.showToast({
											title: '支付取消',
											icon: 'success',
											duration: 800
										});
									}
								});
							});
						}
						if (res.tapIndex === 1) {
							that.$http.HttpRequst.request(true, 'alipay/pay', params, 'POST', res => { //支付宝支付
								uni.requestPayment({
									provider: 'alipay',
									orderInfo: res, //订单数据
									success: function(res) {
										uni.showToast({
											title: '支付成功',
											icon: 'success',
											duration: 800,
											success() {
												setTimeout(res => {
													that.page = 1
													that.onReachBottomshow = true
													that.getData()
												}, 800)
											}
										});
									},
									fail: function(err) {
										uni.showToast({
											title: '支付取消',
											icon: 'success',
											duration: 800
										});
									}
								});
							});
						}
					}
				});
			} else { // 余额支付
				uni.showActionSheet({
					itemList: ['余额支付'],
					success: function(res) {
						if( res.tapIndex == 0 ){
							if( uni.getStorageSync('userinfo').balance < Number(allprice) ){
								uni.showToast({
									title: '余额不足，请充值',
									icon: 'success',
									duration: 600,
									success() {
										setTimeout( res => {
											uni.redirectTo({
												url: '/pages/info/interests/interests'
											})
										},600)
									}
								});
							}else{
								that.$http.HttpRequst.request(false, 'order/balancePay', params, 'POST', res => {
									if( res.code == 200 ){
										uni.showToast({
											title: '成功',
											icon: 'success',
											duration: 600,
											success() {
												setTimeout( res => {
													that.page = 1
													that.onReachBottomshow = true
													that.getData()
												},800)
											}
										});
									}
								});
							}
						}
					}
				});
			}


		},
		getData() {
			var that = this;
			let params = {
				status: this.status,
				page: this.page,
				_token: uni.getStorageSync('userinfo')._token
			}
			this.$http.HttpRequst.request(true, 'user/myOrder', params, 'POST', res => {
				if (res.code == 200) {
					if (that.page == 1) {
						that.list = res.data
					} else {
						if (res.data.data == '') {
							that.onReachBottomshow = false
						} else {
							that.list.data = that.list.data.concat(res.data.data);
						}
					}
				}
			});
		},
	}
}
